import Vue from 'vue'
import Router from 'vue-router'


const main = resolve => require(['@/components/page/main.vue'], resolve)
const login = resolve => require(['@/components/login.vue'], resolve)
const joke = resolve => require(['@/components/page/joke.vue'], resolve)
const news = resolve => require(['@/components/page/news.vue'], resolve)
const imgs = resolve => require(['@/components/page/imgs.vue'], resolve)
const home = resolve => require(['@/components/page/home.vue'], resolve)
const today = resolve => require(['@/components/page/today.vue'], resolve)
const JokeList = resolve => require(['@/components/JokeList.vue'], resolve)
const addJoke = resolve => require(['@/components/addJoke.vue'], resolve)

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: login
    },
    {
      path: '/main',
      component: main,
      children:[
        {
          path: '/main',
          component: main
        },
        {
          path: '/joke',
          component: joke
        },
        {
          path: '/news',
          component: news
        },
        {
          path: '/imgs',
          component: imgs
        },
        {
          path: '/home',
          component: home
        },
        {
          path: '/today',
          component: today
        },
        {
          path: '/addJoke',
          component: addJoke
        },
        {
          path: '/JokeList',
          component: JokeList
        }
      ]
    }
  ]
})



